import React from 'react'
import { Link } from 'react-router'
import { prefixLink } from 'gatsby-helpers'
import Helmet from 'react-helmet'
import { config } from 'config'
import _ from 'underscore'

import Logo from '../components/logo.js'
import { Wrapper, Content, Links, Footer } from '../components/styled-components.js'

export default class Index extends React.Component {
  render () {
    return (
      <Wrapper>
        <Helmet
          title={config.siteTitle}
          meta={[
            {"name": "description", "content": "VulcanJS: The full-stack React+GraphQL framework"},
          ]}
          link={[
            {rel: "shortcut icon", href: "images/vulcan-logo-border.png"},
          ]}
        />
        <div/>
        <Content>
          <Logo />
          <Links>
            <a href="http://nova-docs.telescopeapp.org">Documentation</a>
            &nbsp;|&nbsp;
            <a href="https://github.com/TelescopeJS/Telescope/">GitHub</a>
            &nbsp;|&nbsp;
            <a href="https://medium.com/vulcanjs">Blog</a>
            &nbsp;|&nbsp;
            <a href="https://slack.telescopeapp.org">Slack</a>
          </Links>
        </Content>
        <Footer>
          Telescope is becoming VulcanJS. <a href="http://nova-docs.telescopeapp.org/vulcan.html">Learn more</a>. 
        </Footer>
      </Wrapper>
    )
  }
}



